<template>
    <div class="wallet">
      <div class="count_top header">
        <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
        <span>我的钱包</span>
        <img src='../../assets/person/menu.png' alt="" @click="setPass">
      </div>
      <div class="center">
	      <div>账户余额</div>
	      <div><span>￥</span>0.00</div>
	      <div @click="balance">余额明细></div>
	      <div @click="baldetail">提现</div>
      </div>
    </div>
</template>
<script>
  import { Toast,MessageBox,Indicator} from 'mint-ui';
  export default {
    data (){
      return {
          lists: [
            {name: '麦粒', desc: '可查看商城积分'},
            {name: '麦穗', desc: '三种套餐奖励'},
            {name: '乐豆', desc: '查看乐豆数量, 可每天领取'}
          ],
        withdraw: {

        }
      }
    },
    methods: {
      baldetail(){
      	this.$router.push('/baldetail')
      },
      balance(){
      	this.$router.push('/balance')
      },
      setPass(){
        this.$router.push('/setPayPass')
      },
      seeRecord(){
        this.$router.push('/withdrawRecord')
      },
      withDraw(){
          let money = this.withdraw.money;
          let minMoney = this.withdraw.min_money;
          let maxMoney = this.withdraw.max_money;
          let payType = this.withdraw.pw;
          let bank = this.withdraw.is_bank;
          if (payType === 1) {
            if (bank === 1) {
              if (minMoney < money) {
                this.$router.push('/withdraw');
              } else {
                Toast({
                  message: `提现金额至少${minMoney}`,
                  duration: 1000
                });
              }
            } else {
              this.$router.push('/bindCard/-1');
            }
          } else {
            //0设置 1修改
            this.$router.push('/payword/0');
          }
      },
      back (){
        this.$router.go(-1);
      },
      seeType(item) {
        switch (item.name){
          case '麦粒':
            this.$router.push('/kernel');
            break;
          case '麦穗':
            this.$router.push('/ear');
            break;
          case '乐豆':
            this.$router.push('/sign');
            break;
        }
      },
      // 获取余额
      getWithdrawInfo(){
        this.post('home/Withdraw/getWithdrawInfo', {}).then(res => {
          if (res.data.code === 10000) {
            this.withdraw = res.data.info;
          }
        })
      },
    },
    mounted(){
      this.getWithdrawInfo();
    }
  }
</script>
<style scoped>
@import "../../../static/header.css";
.header img:last-child {
  width: 0.36rem;
  margin-right: 0.4rem;
}
.header span {
  margin: 0;
}
  .wallet {
    height: 100vh;
    background-color: #F2F2F2;
  }
  .main {
    padding: 0.2rem;
  }
  .wallet_card {
    padding: 0.6rem 0.4rem;
    background-color: #ff6f00;
    color: #FFFFFF;
    border-radius: 0.1rem;
  }
  .wallet_title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.27rem;
  }
  .wallet_title>p {
    font-size: 0.24rem;
  }
  .wallet_card {
    position: relative;
    overflow: hidden;
  }
  .wallet_card h2 {
    font-size: 0.48rem;
    margin-bottom: 0.4rem;
  }
  .wallet_card img {
    width: 2.44rem;
    position: absolute;
    bottom: -0.2rem;
    right: 0;
  }
  .reflect {
    display: inline-block;
    height: 0.48rem;
    text-align: center;
    line-height: 0.48rem;
    width: 1.2rem;
    font-size: 0.3rem;
    background-color: #ffa800;
    border-radius: 0.2rem;
    color: #FFFFFF;
    margin-right: 0.1rem;
  }
  .way {
    height: 1.36rem;
    background-color: #FFFFFF;
    margin: 0.2rem 0;
    padding: 0 0.2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
   .way_l p {
     font-size: 0.36rem;
     color: #111111;
     margin-bottom: 0.1rem;
  }
  .way_l span {
    color: #666666;
    font-size: 0.24rem;
  }
  /**/
 .center{text-align: center;}
 
 .center div:nth-of-type(1){margin: 1rem auto .5rem;}
 .center div:nth-of-type(2){color: #444;font-size: .8rem;margin-bottom: .5rem;}
 .center div span{font-size: .5rem;}
 .center div:nth-of-type(3){color: #FF4A65;}
 .center div:nth-of-type(4){color: #fff;background: #FF4A65;padding: .2rem;border-radius: 1rem;margin: .4rem;margin-top: 2rem;}
</style>



